<template>
	<view class="body">
		<view class="content">
			<!-- 选择器 -->
			<!-- <view class="tab">
				<u-tabs :list="list1" @click="changeTab" lineWidth="100rpx" lineHeight="6rpx" lineColor="#ffd84d"
					inactiveStyle="font-size:30rpx;" activeStyle="font-size:34rpx;font-weight:600;color:#000;"></u-tabs>
			</view> -->
			<!-- 订阅列表 -->
			<view class="list">
				<view class="tips" v-if="data.data.length<1">暂无收藏~</view>
				<view class="cell" v-for="(item,index) in data.data" :key="index" @click="toDetial(item)">
					<view class="icon">
						<view class="notRead" v-if="item.state==0">未读</view>
						<view class="ride" v-else>已读</view>
					</view>
					<view class="title">{{item.title || "未知"}}</view>
					<view class="situation">
						<view class="belonging">
							<view>学校：{{item.schoolName || "未知"}}</view>
							<view>专业：{{item.majorName || "未知"}}</view>
						</view>
						<view class="num">招生人数：{{item.num || 0}}人</view>
						<view class="times">
							<view>更新时间：{{item.updatetime}}</view>
							<view class="look">{{item.click || 0}}次</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		collist, colcon
	} from '@/common/http.api.js';
	export default {
		data() {
			return{
				token:'',
				pages:1,
				allPage:'',
				current:0,
				id:'',
				type:'school',
				data:[],
				isLastPage:false, //是否是最后一页的数据
				name:'', //当前类名
				list1: [{
					name: '我订阅的学校',
				}, {
					name: '我订阅的专业',
				}],
			}
		},
		// 滑动到页面底部加载下一页
		onReachBottom() {
			// 判断是否有数据
				if(this.isLastPage==true){
					this.$u.toast("暂无更多")
				}else{
					this.pages++
					console.log(this.pages)
					let params = {
						token:this.token, 
						type:this.type,
						page:this.pages,
						totalNum:10,
						conid:this.id
					}
					colcon({...params}).then(res=>{
						console.log(res)
						if(res.data.code==-1){
							this.$u.toast(res.data.data)
						}
						res.data.data.data.length<10 ? this.isLastPage = true : ''
						this.data.data.push(...res.data.data.data)
					}).catch(err=>{
						console.log(err)
					})
				}
				// this.getData(); // 获取数据
				console.log('我滚动到底部了')
		},
		onLoad(option) {
			this.id = option.id
			this.current = option.current
			this.name = option.name
		},
		onShow() {
			this.token = store.state.vuex_token
			this.pages = 1
			this.current == 0 ? this.type='school' : this.type='major'
			let params = {
				token:this.token, 
				type:this.type,
				page:this.pages,
				totalNum:10,
				conid:this.id
			}
			colcon({...params}).then(res=>{
				console.log(res)
				this.data = []
				if(res.data.code==-1){
					this.$u.toast(res.data.data)
				}
				res.data.data.data.length<10 ? this.isLastPage = true : ''
				this.data = res.data.data
				this.allPage = res.data.data.total
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			toDetial(item){
				uni.navigateTo({
					url:'/pages/index/details?id='+item.id+'&status=1'
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 1vh 0;
		min-height: 98vh;
		background-color: #f7f6fb;
		.content{
			margin: 0 5%;
			border-radius: 20rpx;
			padding: 0 20rpx;
			.tab {
				margin: 30rpx 0;
				display: flex;
				justify-content: center;
				.u-tabs__wrapper__nav__item {
					padding: 0 70rpx;
				}
			}
			.list {
				.tips{
					text-align: center;
					font-size: 24rpx;
					padding: 20rpx;
					padding-top: 0;
				}
				.cell {
					padding-bottom: 30rpx;
					margin-bottom: 30rpx;
					background-color: #fff;
					border-radius: 20rpx;
					position: relative;
					.icon{
						position: absolute;
						right: 30rpx;
						top: 30rpx;
						view{
							color: #fff;
							border-radius: 50rpx;
							font-size: 24rpx;
							padding: 6rpx 20rpx;
						}
						.notRead{
							background-color: #17d6a2;
						}
						.ride{
							background-color: #e5e5e5;
						}
					}
					.title {
						font-size: 32rpx;
						margin: 0 30rpx;
						padding: 30rpx 0;
						padding-left: 70rpx;
						border-bottom: 2rpx solid #f7f6fb;
						background-image: url(../../static/index/icon.png);
						background-repeat: no-repeat;
						background-position: 0rpx center;
						background-size: 40rpx;
						margin-bottom: 20rpx;
						width: 65%;
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
					}
					.situation {
						margin: 0 30rpx;
						width: 85%;
						font-family: cursive;
						.belonging {
							font-size: 28rpx;
							display: flex;
							font-weight: 300;
							justify-content: space-between;
							color: #929292;
							line-height: 48rpx;
							view{
								width: 400rpx;
								text-overflow: ellipsis;
								white-space: nowrap;
								overflow: hidden;
							}
						}
						.num {
							font-size: 28rpx;
							font-weight: 300;
							color: #929292;
							line-height: 48rpx;
						}
						.times {
							display: flex;
							justify-content: space-between;
							align-items: center;
							font-size: 26rpx;
							font-weight: 300;
							color: #929292;
							.look {
								background-color: #fedbd8;
								color: #fa4a3e;
								padding: 6rpx 14rpx;
								padding-left: 48rpx;
								border-radius: 50rpx;
								background-image: url(../../static/index/look.png);
								background-repeat: no-repeat;
								background-position: 12rpx center;
								background-size: 30rpx;
							}
						}
					}
				}
			}
		}
	}
</style>